import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
 import { SaleAutoMationService } from '../shared/sales-automation-service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute, Router } from '@angular/router';
import { globalVariableService } from '../../common/globalService';
 
@Component({
  selector: 'app-free-trial',
  templateUrl: './free-trial.component.html',
  styleUrls: ['./free-trial.component.scss'],
})
export class FreeTrialComponent implements OnInit {
  partnerId:any
  constructor(
    private _saleAutoMationService: SaleAutoMationService,
    private _globalVariableService:globalVariableService,
    private _toastrService: ToastrService,
    private route: ActivatedRoute
  ) {
    this.partnerId = this.route.snapshot.paramMap.get('pId');
    if (this.partnerId)
      this._globalVariableService.setPartnerId(this.partnerId);
    else this.partnerId = this._globalVariableService.getPartnerId();
  }

  ngOnInit(): void {
   
  }
}
