import { Component, OnInit } from '@angular/core';
import { globalVariableService } from '../../common/globalService';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss'],
})
export class FaqComponent implements OnInit {
  partnerId: any;
  constructor(
    private _globalVariableService: globalVariableService,
    private router: ActivatedRoute
  ) {
    this.partnerId = this.router.snapshot.paramMap.get('pId');
    if (this.partnerId)
      this._globalVariableService.setPartnerId(this.partnerId);
    else this.partnerId = this._globalVariableService.getPartnerId();
  }

  ngOnInit(): void {}
}
