import { NgModule } from '@angular/core';
import { SalesAutomationRoutingModule } from './sales-automation-routing.module';
import { HeaderComponent } from './header/header.component';
import { SalesAutomationHomeComponent } from './sales-automation-home.component';
import { FooterComponent } from './footer/footer.component';
import { PricingComponent } from './../sales-automation-home/pricing/pricing.component';
import { CommonModule } from '@angular/common';
import { FreeTrialComponent } from './free-trial/free-trial.component';
import { FormsModule } from '@angular/forms';
import { DemoComponent } from './demo/demo.component';
import { FaqComponent } from './faq/faq.component';
import { NoSalesPartnerExistComponent } from './no-sales-partner-exist/no-sales-partner-exist.component';
import { HomeComponent } from './home/home.component';
import {
  FaIconLibrary,
  FontAwesomeModule,
} from '@fortawesome/angular-fontawesome';
import { ActivatedRoute, RouterModule } from '@angular/router';
import { AppCommonModule } from '../common/common.module';
import { NgxCaptchaModule } from 'ngx-captcha';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import {
  faCheckSquare,
  faRssSquare,
  faSquare,
  faPlus,
  faMinus,
  faTrash,
  faCheck,
  faWindowClose,
  faTimes,
} from '@fortawesome/free-solid-svg-icons';
import { OneMonthProductComponent } from './one-month-product/one-month-product.component';

@NgModule({
  declarations: [
    HeaderComponent,
    SalesAutomationHomeComponent,
    FooterComponent,
    PricingComponent,
    HomeComponent,
    FreeTrialComponent,
    DemoComponent,
    FaqComponent,
    NoSalesPartnerExistComponent,
    OneMonthProductComponent,
  ],
  imports: [
    FormsModule,
    NgxCaptchaModule,
    CommonModule,
    BsDatepickerModule.forRoot(),
    NgbModule,
    FontAwesomeModule,
    AppCommonModule,
    SalesAutomationRoutingModule,
  ],
  exports: [AppCommonModule],
})
export class SalesAutomationModule {
  partnerId: any;
  constructor(private library: FaIconLibrary, private router: ActivatedRoute) {
    library.addIcons(
      faSquare,
      faCheckSquare,
      faRssSquare,
      faPlus,
      faMinus,
      faTrash,
      faCheck,
      faWindowClose,
      faTimes
    );
    this.partnerId = this.router.snapshot.paramMap.get('pId');
  }
}
