import { Component, OnInit} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { globalVariableService } from '../../common/globalService';
export class Demo {
  Name?: string;
  Contact?: string;
  Mail?: string;
  Date?: string;
  Time?: string;
  partnerId?:string
}
@Component({
  selector: 'app-demo',
  templateUrl: './demo.component.html',
  styleUrls: ['./demo.component.scss'],
})
export class DemoComponent implements OnInit {
  partnerId: any;
  constructor(
    private _globalVariableService: globalVariableService,
    private route: ActivatedRoute,
  ) {
    this.partnerId = this.route.snapshot.paramMap.get('pId');
    if (this.partnerId)
      this._globalVariableService.setPartnerId(this.partnerId);
    else this.partnerId = this._globalVariableService.getPartnerId();
  }
  ngOnInit(): void {
    this.partnerId = this._globalVariableService.getPartnerId();
  }
}
