import { NgModule, Component } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SalesAutomationHomeComponent } from './sales-automation-home.component';
import { PricingComponent } from './pricing/pricing.component';
import { FreeTrialComponent } from './free-trial/free-trial.component';
import { DemoComponent } from './demo/demo.component';
import { FaqComponent } from './faq/faq.component';
import { NoSalesPartnerExistComponent } from './no-sales-partner-exist/no-sales-partner-exist.component';
import { HomeComponent } from './home/home.component';
import { OneMonthProductComponent } from './one-month-product/one-month-product.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/',
    pathMatch: 'full',
  },
  {
    path: ':pId',
    component: HomeComponent,
  },
  {
    path: 'salesautomation/:pId',
    component: HomeComponent,
  },
  {
    path: 'p/pricing/:pId',
    component: PricingComponent,
  },
  {
    path: 'one/month/:pId',
    component: OneMonthProductComponent,
  },
  {
    path: 'app/demo/:pId',
    component: DemoComponent,
  }, 
  {
    path: 'questions/faqs/:pId',
    component: FaqComponent,
  },
  {
    path: 'no/sales/partner/exist/:pId',
    component: NoSalesPartnerExistComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SalesAutomationRoutingModule {}
